import { useEffect, useState } from "react";
import { contactService } from "../../services/contact.service";
import { useParams } from "react-router-dom";
import { DUMMY_USER_URL } from "../../config/config";
import { ReactSVG } from "react-svg";
import { useDispatch, useSelector } from "react-redux";
import { addFavorite, deleteFavorite } from "../../store/user/user-action";
import { utilService } from "../../services/util.service";
import { Preloader } from "../../components";
import { contactTransType } from "../../constants/contactTransType";
import cogoToast from "@hasanm95/cogo-toast";
import { addContactToCartAction } from "../../store/cart/cart-action";


const ContactDetails = () => {
    const { currentUser } = useSelector(state => state.user)

    const [contact, setContact] = useState(null)
    const [isFavorite, setIsFavorite] = useState(false)
    const [isLoading, setIsLoading] = useState(true)
    const [isOwned, setIsOwned] = useState(false)

    const { id } = useParams()
    const dispatch = useDispatch()

    useEffect(() => {
        loadContact()
        checkIsOwnedByUser()
    }, [])

    const checkIsOwnedByUser = () => {
        if (currentUser) {
            const isOwned = currentUser.contactTransactions.find(trans => (trans.type === contactTransType.contactPurchase) && (trans.contact._id === id)) ? true : false
            setIsOwned(isOwned)
        } else setIsOwned(false)
    }

    const loadContact = async () => {
        try {
            const contact = await contactService.getById(id)
            loadFavorite(contact)
            setContact(contact)
        } catch (err) {
            console.log(err)
        } finally {
            setIsLoading(false)
        }
    }

    const loadFavorite = (contact) => {
        if (currentUser) {
            const isFavorite = currentUser.favorites.find(favorite => favorite._id === contact._id)
            if (isFavorite) setIsFavorite(true)
            else setIsFavorite(false)
        }
    }

    const onFavorite = (item) => {
        if (isFavorite) {
            deleteFavorite(dispatch, item, currentUser)
            setIsFavorite(false)
        }
        else {
            addFavorite(dispatch, item, currentUser)
            setIsFavorite(true)
        }
    }

    const isUserOwnContact = () => {
        if (!contact || !currentUser) return false
        const res = currentUser.contactTransactions.find(trans => trans.contact._id === contact._id)
        return res ? true : false
    }

    const isUserHasCredits = () => {
        if (currentUser.credits >= utilService.getContactValueInCredit(contact.price)) return true
        else return false
    }

    const onAddToCart = async () => {
        if (isUserOwnContact()) {
            return cogoToast.info("You own this contact already", { position: "center-top" });
        }

        if (!isUserHasCredits()) {
            return cogoToast.info("Not enough credits", { position: "center-top" });
        }
        addContactToCartAction(dispatch, contact)
        return cogoToast.success("Contact added to cart", { position: "center-top" });
    }

    const onDownload = () => {

    }

    if (isLoading || !contact) return <Preloader />;
    return (
        <div className="body-wrapper space-pb--120">
            {/*====================  contact image ====================*/}
            <div className="product-image-slider-wrapper space-pb--30 space-mb--30">
                <div className="product-image-single swiper-slide">
                    <img
                        src={contact?.img?.url || DUMMY_USER_URL}
                        className="img-fluid"
                        alt=""
                    />
                </div>
            </div>

            {/*====================  contact content ====================*/}
            <div className="product-content-header-area border-bottom--thick space-pb--30">
                <div className="container">
                    <div className="row">
                        <div className="col-12">
                            <div className="product-content-header">
                                <div className="product-content-header__main-info">
                                    {isOwned && <h3 className="title">Name: <span className="info">{contact.name} {contact.familyName}</span></h3>}
                                    <h3 className="title">Category: <span className="info">{contact.category}</span></h3>
                                    <h3 className="title">Company: <span className="info">{contact.company}</span></h3>
                                    <h3 className="title">Job Title: <span className="info">{contact.jobTitle}</span></h3>
                                    <div className="price">
                                        <span className="discounted-price">{`$${contact.price}`}</span>
                                    </div>
                                </div>
                                <div className="product-content-header__wishlist-info text-center">
                                    <ReactSVG
                                        src={
                                            process.env.PUBLIC_URL +
                                            "/assets/img/icons/heart-dark.svg"
                                        }
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            {/* product content description */}
            <div className="product-content-description border-bottom--thick space-pt--25 space-pb--25">
                <div className="container">
                    <div className="row">
                        <div className="col-12">
                            <p className="space-mb--25">Description: {contact.desc}</p>
                            <h4 className="space-mb--5">Information</h4>
                            <p>
                                To Bangladesh from seller via china. Shipping <br /> method
                                online.
                            </p>
                        </div>
                    </div>
                </div>
            </div>
            {/* product content safety */}
            <div className="product-content-safety border-bottom--thick space-pt--15 space-pb--15">
                <div className="container">
                    <div className="row">
                        <div className="col-12">
                            <h4>
                                <ReactSVG
                                    src={
                                        process.env.PUBLIC_URL + "/assets/img/icons/security.svg"
                                    }
                                />{" "}
                                Secure Payment Method.
                            </h4>
                        </div>
                    </div>
                </div>
            </div>

            {/* shop product button */}
            <div className="shop-product-button">
                <button className="wishlist" disabled={!isFavorite} onClick={isOwned ? () => { } : onFavorite}>
                    {isOwned || isFavorite ? "Feedback" : "Add to favorites"}
                </button>
                <button className="cart" onClick={isOwned ? onDownload : onAddToCart}>
                    {isOwned ? "Download" : "Add to cart"}
                </button>
            </div>
        </div>
    )
}

export default ContactDetails