import { ContactsEP } from "../config/config"
import { httpService } from "./http.service"

export const contactService = {
    query,
    getById,
    getUserContactSales,
    queryByCategory,
    getLastCreatedContacts,
}


async function query(filterBy = {}) {
    try {
        const res = await httpService.post(ContactsEP.get, filterBy)
        if (res.status === 'ok') return res.content
        else return new Error('Cannot get contacts')
    } catch (err) {
        throw err
    }
}

async function getById(id) {
    try {
        const res = await httpService.get(`${ContactsEP.getById}/${id}`)
        if (res.status !== 'ok') throw new Error('Cannot find contact')
        else return res.content
    } catch (err) {
        throw err
    }
}

async function getUserContactSales(userId) {
    try {
        const res = await httpService.post(`${ContactsEP.getByUser}`, { userId })
        if (res.status === 'ok') return res.content
        else return new Error('Cannot get contacts sales')
    } catch (err) {
        throw err
    }
}

async function queryByCategory(cat) {
    try {
        const res = await httpService.get(`${ContactsEP.get}/${cat}`)
        return res
    } catch (err) {
        throw err
    }
}

async function getLastCreatedContacts() {
    try {
        const res = await httpService.get(`${ContactsEP.new}/10?DESC`)
        return res
    } catch (error) {
        throw error
    }
}