import { addCreditToCart, addToContactCart, deleteFromCart } from "./cart-slice"


export const addCreditToCartAction = (dispatch, credit) => {
    dispatch(addCreditToCart(credit))
}

export const addContactToCartAction = (dispatch, contact) => {
    dispatch(addToContactCart(contact))
}

export const removeContactToCartAction = (dispatch, contactId) => {
    dispatch(deleteFromCart(contactId))
}